.App {
  text-align: center;
  padding-bottom: 100px;
}

.start {
  opacity: 0;
}

.end {
  opacity: 1;
  transition: opacity 0.5s;
}

/* 최근 본 상품 */
.watched {
  width: auto;
  height: auto;
  position: fixed;
  right: 10px;
  bottom: 100px;
  border: 1px solid #686868;
  color: #686868;
  background: rgba(255,255,255,0.5);
  pointer-events: none;
}

.watched p {
  font-size: clamp(1rem, 4vw, 1.4rem);
  /*첫 번째 인자는 최소값, 두 번째 인자는 상대 단위값을 갖는 글자 크기, 세 번째 인자는 최대값*/
  user-select: none;
}

/* detail */
.linkWrap {
  width: 100%;
  height: auto;
  background: #eee;
}

.linkWrap a {
  display: inline-block;
  text-decoration: none;
  color: #000;
  margin-right: 25px;
  line-height: 50px;
}

.linkWrap a:last-child {
  margin-right: 0px;
}

.linkWrap a:hover {
  color: #00000054;
}